
import { defineComponent, ref } from 'vue';
import { useField, useFormErrors } from 'vee-validate'
import * as yup from 'yup'

    export const usuarioComponentSchema = yup.object({
        usuario: yup.object({
            nome: yup.string().required("Nome  obrigatrio"),
            email: yup.string().required("E-mail  obrigatrio").email("E-mail no  vlido!"),
            telefone: yup.string().required("Telefone  obrigatrio"),
            senha: yup.string().required("Senha  obrigatrio").min(8, "Senha deve ter no mnimo 8 caractres."),
        })  
    })

    export default defineComponent({
        setup(){

            const { value: nome } = useField("usuario.nome");
            const { value: email } = useField("usuario.email");
            const { value: telefone } = useField("usuario.telefone");
            const { value: senha } = useField("usuario.senha");

            return{
                nome,
                email,
                telefone,
                senha,
                errors : useFormErrors() as any,                
            }
        }
    })
